


























import Vue from 'vue'

export default Vue.extend({
  name: 'MenuItem',
  props: {
    menu: {
      type: Object,
      required: true
    }
  },
  methods: {
    transform (href: string) {
      switch (href) {
        case 'Role':
          return '/role'
        case 'Menu':
          return '/menu'
        case 'Resource':
          return '/resource'
        case 'Courses':
          return '/course'
        case 'Users':
          return '/user'
        case 'Advertise':
          return '/advert'
        case 'AdvertiseSpace':
          return '/advert-space'
        default:
          return ''
      }
    }
  }
})
